import { createApp } from 'vue';
import { createRouter, createWebHistory } from 'vue-router';
import { createStore } from 'vuex';

import VueParticles from 'vue-particles';

import App from './App.vue';
import TheHome from "./components/TheHome.vue";
import TheAbout from "./components/TheAbout.vue";
import TheInterest from "./components/TheInterest.vue";
import TheProject from "./components/TheProject.vue";
import TheBlog from "./components/TheBlog.vue";
import TheTestimonial from "./components/TheTestimonial.vue";
import TheContact from "./components/TheContact.vue";





// 4px 4px 10px 0 #040a0e, -4px -4px 10px #0e2836,
                  
const router = createRouter({
    history: createWebHistory(),
    routes: [
        {path: '/', component:TheHome,},
        {path: '/about', component:TheAbout,},
        {path: '/interest', component:TheInterest,},
        {path: '/project', component:TheProject,},
        {path: '/blog', component:TheBlog,},
        {path: '/testimonial', component:TheTestimonial,},
        {path: '/contact', component:TheContact,},
    ],
    linkActiveClass: 'nav-active'
});
const store = createStore({
    state() {
        return {
            bgColor: '#091922',
            bgLightShadow:'#0e2836',
            bgDarkShadow:'#040a0e',
            skinColor: 'crimson',
            bgShadow: '6px 6px 8px #040a0e, -6px -6px 8px #0e2836',
            bgShadowSmall: '4px 4px 6px #040a0e, -4px -4px 6px #0e2836',
            bgShadowSmallHover: '4px 4px 10px #040a0e, -4px -4px 10px #0e2836',
            bgShadowSmallActive: `4px 4px 6px 0 #040a0e inset, -4px -4px 6px #0e2836 inset`,
            bgShadowText: '2px 2px 5px #040a0e, -2px -2px 5px #0e2836',

            skinShadow:'9px 9px 21px #580818,-9px -9px 21px #ff2060',
            skinShadowSmall: '4px 4px 6px #580818, -4px -4px 6px #ff2060',
            textColor: '#ffffff',
            textColorLight: 'grey',
        }
    },
    getters: {
        bgColorGetter(state) {
            return state.bgColor;
        },
        bgShadowGetter(state) {
            return state.bgShadow;
        },
        bgShadowSmallGetter(state) {
            return state.bgShadowSmall;
        },
        bgShadowSmallHoverGetter(state) {
            return state.bgShadowSmallHover;
        },
        bgShadowTextGetter(state) {
            return state.bgShadowText;
        },
        skinColorGetter(state) {
            return state.skinColor;
            // return "green";
        },
        skinShadowGetter(state) {
            return state.skinShadow;
        },
        textColorGetterstate(state) {
            return state.textColor;
        },

        textColorLightGetter(state) {
            return state.textColorLight;
        },
    }
})

const app = createApp(App);

app.use(store);
app.use(router);
app.use(VueParticles);

app.mount('#app');
